"use client";

import React, { useState } from "react";
import { DndContext, closestCenter, DragEndEvent } from "@dnd-kit/core";
import {
  SortableContext,
  useSortable,
  verticalListSortingStrategy,
} from "@dnd-kit/sortable";
import { useDroppable } from "@dnd-kit/core";
import { CSS } from "@dnd-kit/utilities";
import { IconChevronDown, IconChevronCompactRight, IconCircle } from "@tabler/icons-react";
import { CircleDotDashed } from "lucide-react";
import moment from "moment";

// Dummy components for demo purposes
const IssueStatus = ({ isOpen, onOpenChange, onActionSelect }) => {
  if (!isOpen) return null;
  return (
    <div className="bg-white border p-2 rounded shadow-lg">
      <p className="text-sm">Issue Status Menu</p>
      <button onClick={() => onActionSelect("Done")} className="text-blue-600">Set to Done</button>
      <button onClick={() => onOpenChange(false)} className="ml-2 text-red-500">Close</button>
    </div>
  );
};

const DropdownMenuIssueAction = ({ isOpen, onOpenChange, dropdownPosition, onActionSelect }) => {
  if (!isOpen) return null;
  return (
    <div
      className="bg-white border p-2 rounded shadow-lg"
      style={{ position: "absolute", top: dropdownPosition?.y, left: dropdownPosition?.x }}
    >
      <p className="text-sm">Context Menu</p>
      <button onClick={() => onActionSelect("Edit")}>Edit</button>
      <button onClick={() => onOpenChange(false)} className="ml-2 text-red-500">Close</button>
    </div>
  );
};

// Types
interface Issue {
  id: number;
  title: string;
  description: string;
  createdAt: string;
  type: string;
}

function SortableItem({
  issue,
  sectionType,
  onRightClick,
  onStatusClick,
}: {
  issue: Issue;
  sectionType: string;
  onRightClick: (e: React.MouseEvent) => void;
  onStatusClick: (issue: Issue) => void;
}) {
  const { attributes, listeners, setNodeRef, transform, transition } = useSortable({
    id: issue.id.toString(),
    data: { type: sectionType, id: issue.id },
  });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  return (
    <li
      ref={setNodeRef}
      style={style}
      {...attributes}
      {...listeners}
      onContextMenu={(e) => {
        e.preventDefault();
        onRightClick(e);
      }}
      className="flex justify-between items-center p-2 border-b cursor-pointer hover:bg-gray-50"
    >
      <div className="flex items-center space-x-2">
        <span>STU-{issue.id}</span>
        <span onClick={() => onStatusClick(issue)} className="text-gray-600 cursor-pointer">
          <IconCircle size={17} />
        </span>
        <span>{issue.title}</span>
        <span className="text-gray-400">{issue.description}</span>
      </div>
      <div className="flex items-center space-x-1 text-gray-500 text-sm">
        <CircleDotDashed size={15} />
        <span>{issue.createdAt}</span>
      </div>
    </li>
  );
}

function DroppableContainer({ id, children }: { id: string; children: React.ReactNode }) {
  const { setNodeRef } = useDroppable({ id, data: { type: id } });
  return <div ref={setNodeRef} className="min-h-[40px]">{children}</div>;
}

export function TableDemo() {
  const [dropdownStates, setDropdownStates] = useState({
    todo: true,
    done: true,
  });

  const [data, setData] = useState([
    {
      type: "todo",
      issue_list: [
        {
          id: 1,
          title: "First Task",
          description: "(3)",
          type: "todo",
          createdAt: moment().format("MMM Do YY"),
        },
      ],
    },
    {
      type: "done",
      issue_list: [
        {
          id: 2,
          title: "Completed Task",
          description: "(1)",
          type: "done",
          createdAt: moment().format("MMM Do YY"),
        },
      ],
    },
  ]);

  const [contextMenuPosition, setContextMenuPosition] = useState<{ x: number; y: number } | null>(null);
  const [showContextMenu, setShowContextMenu] = useState(false);
  const [activeIssueForStatus, setActiveIssueForStatus] = useState<Issue | null>(null);

  const handleRightClick = (e: React.MouseEvent) => {
    e.preventDefault();
    setContextMenuPosition({ x: e.clientX, y: e.clientY });
    setShowContextMenu(true);
  };

  const handleStatusClick = (issue: Issue) => {
    setActiveIssueForStatus(issue);
  };

  const onDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (!over) return;

    const sourceType = active.data.current?.type;
    const destinationType = over.data.current?.type;
    if (!sourceType || !destinationType) return;

    const sourceSection = data.find((section) => section.type === sourceType);
    const destinationSection = data.find((section) => section.type === destinationType);
    if (!sourceSection || !destinationSection) return;

    const draggedIndex = sourceSection.issue_list.findIndex((i) => i.id.toString() === active.id.toString());
    const draggedItem = sourceSection.issue_list[draggedIndex];

    if (sourceType === destinationType) {
      const overIndex = destinationSection.issue_list.findIndex((i) => i.id.toString() === over.id.toString());
      const updatedList = [...sourceSection.issue_list];
      updatedList.splice(draggedIndex, 1);
      updatedList.splice(overIndex, 0, draggedItem);

      const updatedSections = data.map((section) =>
        section.type === sourceType ? { ...section, issue_list: updatedList } : section
      );
      setData(updatedSections);
    } else {
      const updatedSourceList = [...sourceSection.issue_list];
      updatedSourceList.splice(draggedIndex, 1);

      const updatedDestinationList = [...destinationSection.issue_list, { ...draggedItem, type: destinationType }];

      const updatedSections = data.map((section) => {
        if (section.type === sourceType) {
          return { ...section, issue_list: updatedSourceList };
        } else if (section.type === destinationType) {
          return { ...section, issue_list: updatedDestinationList };
        }
        return section;
      });

      setData(updatedSections);
    }
  };

  return (
    <DndContext collisionDetection={closestCenter} onDragEnd={onDragEnd}>
      <div className="bg-white rounded-md shadow-md p-4 relative">
        {data.map((section) => (
          <SortableContext
            key={section.type}
            items={section.issue_list.map((issue) => issue.id.toString())}
            strategy={verticalListSortingStrategy}
          >
            <div className="mb-4">
              <div className="flex justify-between bg-gray-100 px-2 py-1 border">
                <span onClick={() => setDropdownStates((prev) => ({ ...prev, [section.type]: !prev[section.type] }))} className="flex items-center cursor-pointer">
                  {dropdownStates[section.type] ? <IconChevronDown size={12} /> : <IconChevronCompactRight size={12} />}
                  <span className="ml-2 capitalize">{section.type} ({section.issue_list.length})</span>
                </span>
                <button className="text-sm text-gray-500 hover:text-gray-700">+</button>
              </div>

              {dropdownStates[section.type] && (
                <DroppableContainer id={section.type}>
                  <ul className="space-y-2">
                    {section.issue_list.map((issue) => (
                      <SortableItem
                        key={issue.id}
                        issue={issue}
                        sectionType={section.type}
                        onRightClick={handleRightClick}
                        onStatusClick={handleStatusClick}
                      />
                    ))}
                  </ul>
                </DroppableContainer>
              )}
            </div>
          </SortableContext>
        ))}

        {/* Right-click Dropdown */}
        {showContextMenu && contextMenuPosition && (
          <DropdownMenuIssueAction
            isOpen={showContextMenu}
            dropdownPosition={contextMenuPosition}
            onOpenChange={setShowContextMenu}
            onActionSelect={(action) => {
              console.log("Context Menu Action:", action);
              setShowContextMenu(false);
            }}
          />
        )}

        {/* Icon-click Issue Status Dropdown */}
        {activeIssueForStatus && (
          <div className="absolute top-20 right-10 z-50">
            <IssueStatus
              isOpen={!!activeIssueForStatus}
              onOpenChange={(open) => {
                if (!open) setActiveIssueForStatus(null);
              }}
              onActionSelect={(action) => {
                console.log("Status Action:", action);
                setActiveIssueForStatus(null);
              }}
            />
          </div>
        )}
      </div>
    </DndContext>
  );
}
